@use '../helpers/abstracts' as *;
.contacts_wrap {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin: 0 auto;
}
.contacts_main_img {
	max-width: 192rem;
	margin-top: 15rem;
	// max-height: 444px;

	& img {
		display: block;
		width: 100%;
		height: 100%;
		object-fit: contain;
		object-position: center;
	}
}
.contact_info {
	display: flex;
	justify-content: center;
	margin-top: 13rem;
	gap: 14rem;
	max-width: 130rem;
	min-height: 23.6rem;
	font-size: 2rem;
	font-weight: 400;
	line-height: 1.5;
}
.contact_block {
	display: flex;
	flex-direction: column;
	// justify-content: center;
	align-items: center;
	max-width: 21.6rem;
	&.numbers {
		line-height: 1;
	}
}
.contact_title {
	font-size: 2.6rem;
	font-weight: 700;
	line-height: 1.4;
	margin-top: 1rem;
}
.contact_location {
	max-width: 142rem;
	min-height: 50rem;
	& img {
		display: block;
		width: 100%;
		height: 100%;
		object-fit: contain;
		object-position: center;
	}
}

.numbers {
	& a {
		color: $color_text_2;
		text-decoration: none;
	}
}

.social_links {
	display: flex;
	flex-direction: column;
	& a {
		text-decoration: none;
		color: #8a8a8a;
	}
}
.icon {
	max-width: 4.6rem;
	min-height: 4.6rem;
	& img {
		display: block;
		width: 100%;
		height: 100%;
		object-fit: contain;
		object-position: center;
	}
}
.form_wrap {
	margin: 0 auto;
	display: flex;
	justify-content: center;
	max-width: 192rem;
	background-color: $color_section_bg;
}
.form_girl_img {
	max-width: 57.4rem;
	min-height: 81.8rem;
	overflow: hidden;
	& img {
		display: block;
		width: 100%;
		height: 110%;
		object-fit: contain;
		object-position: center;
	}
}
.form_call-back {
	display: flex;
	flex-direction: column;
	justify-content: center;
	max-width: 94rem;
	min-height: 5.8rem;
	& span {
		font-size: 2.6rem;
		font-weight: 700;
		line-height: 1.2;
		text-align: left;
	}
}
.info_input {
	display: flex;
	flex-direction: column;
	max-width: 94rem;
	& input {
		max-width: 100%;
		min-height: 7rem;
		margin-top: 1rem;
		font-size: 2.8rem;
		font-weight: 400;
		line-height: 1;
		padding-left: 5rem;
		border: 1px solid #9e9e9e;
	}
	.textarea {
		min-height: 12.2rem;
		padding-bottom: 4rem;
	}

	& .info_input_descr {
		display: block;
		margin-top: 5rem;
		font-size: 2.6rem;
		font-weight: 400;
		line-height: 1.2;
		text-align: left;
		color: $color_descr_1;
		position: relative;
		& img {
			position: absolute;
			display: block;
			width: 2.8rem;
			height: 2.8rem;
			top: 6.5rem;
			left: 1.5rem;
		}
	}
}
.btn_form {
	margin-top: 5.6rem;
}
.btn_form_in {
	font-size: 2rem;
	display: flex;
	width: 28rem;
	height: 9rem;
	border-radius: 5rem;
	border: 2px solid #28bd25;
	text-transform: uppercase;
	align-items: center;
	justify-content: center;
	text-decoration: none;
	color: #28bd25;
}
.btn_form_in:hover {
	color: white;
	background-color: #28bd25;
	transition-duration: 0.6s;
}
